/* src/styles/Sidebar.css */

.sidebar {
  width: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  background-color: #f4f4f4;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  padding-top: 50px;
  transition: width 0.3s ease;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

.sidebar.open {
  width: 250px;
  padding: 50px 20px 20px;
}

.sidebar h2 {
  font-size: 1.2em;
  color: #333;
  border-bottom: 2px solid #ccc;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.sidebar-header {
  padding: 10px 15px;
  text-align: center;
}

.sidebar-user-name {
  font-weight: bold;
  font-size: 1.2em;
  color: #333;
  margin-bottom: 10px;
}

.sidebar-history-button {
  width: 100%;
  padding: 8px;
  background-color: #2196F3;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9em;
  transition: background-color 0.2s;
}

.sidebar-history-button:hover {
  background-color: #0b7dda;
}

.sidebar-history-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.sidebar-divider {
  border: 0;
  height: 1px;
  background-color: #e0e0e0;
  margin: 10px 0;
}

.toggle-button {
  position: absolute;
  top: 10px;
  left: 100%;
  margin-left: 10px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1.5em;
  line-height: 1;
  cursor: pointer;
  z-index: 101;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.toggle-button:hover {
  background-color: #f0f0f0;
}

.menu-section {
  margin-bottom: 20px;
}

.options-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 10px;
}

.sidebar-button {
  padding: 10px 5px;
  background-color: #e0e0e0;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s;
}

.sidebar-button:hover {
  background-color: #ccc;
}

.sidebar-button.active {
  background-color: #007BFF;
  color: white;
}

.sidebar-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.start-button {
  width: 100%;
  padding: 15px;
  background-color: #28a745;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: auto;
  transition: background-color 0.2s;
}

.start-button:hover {
  background-color: #218838;
}

.start-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

@media (max-width: 600px) {
  .sidebar {
    width: 0;
  }
  .sidebar.open {
    width: 100%;
  }
  .toggle-button {
    left: auto;
    right: 10px;
    margin-left: 0;
    z-index: 102;
  }
}